<template>
  <div>
    <div>
      {{ message }}
    </div>
    <div>
      <h3 class="header"> TITLE </h3>
      <input type="text" v-model='card.title' maxlength="20">
    </div>
    <div>
      <h3 class="header"> TASK </h3>
      <textarea class="input-card" cols="40" rows="2" v-model='card.text' maxlength="100"></textarea>
    </div>
    <div>
      <button v-on:click='saveText'>保存</button>
      <button v-on:click='clearText'>クリア</button>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  data () {
    return {
      card: {
        title: '',
        text: ''
      },
      lists: [],
      message: ''
    }
  },
  methods: {
    clearText () {
      this.card.title = ''
      this.card.text = ''
      this.message = ''
    },
    saveText () {
      if (!this.card.title || !this.card.text) {
        this.message = 'タイトルもしくは内容が空欄です！'
        return
      }
      const path = 'http://localhost:5000/api/add'
      let params = new URLSearchParams()
      params.append('title', this.card.title)
      params.append('text', this.card.text)
      axios.post(path, params)
        .then(response => {
          let id = response.data
          let task = {'id': id, 'text': this.card.text, 'title': this.card.title}
          this.lists.unshift(task)
        }).catch(error => {
          console.log(error)
        })
      this.clearText()

      // this.lists.push({ title: this.card.title, text: this.card.text })
      // this.clearText()
      this.$emit('addCard', { list: this.lists })
    }
  }
}
// # sourceURL=form.js
</script>
<style>
  .input-card {
    width:160px;
    height:50px;
  }

  .header {
    color: #42b983;
  }
</style>
