<template>
  <div id="app">
    <Form @addCard="onAddCard"></Form>
    <Place :list="list"></Place>
  </div>
</template>
<script>
import Form from '@/components/Form'
import Place from '@/components/Place'
import axios from 'axios'

export default {
  name: 'home',
  components: {
    Form,
    Place
  },
  data () {
    return {
      list: []
    }
  },
  methods: {
    onAddCard ({ list }) {
      this.list = list
    }
  },
  created: function () {
    const path = 'http://localhost:5000/api/get'
    axios.get(path)
    .then(response => {
      this.list = response.data
    })
    .catch(error => {
      console.log(error)
    })
  },
}
</script>

